header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    background: linear-gradient(135deg, var(--color-bg) 0%, var(--color-bg-variant) 100%);
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.header__container h1 {
    background: linear-gradient(45deg, var(--color-primary), #00d4ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-size: 3.5rem;
    font-weight: 700;
    margin: 1rem 0;
}


/* CTA */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* header Socials */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials a {
    transition: var(--transition);
    padding: 0.5rem;
    border-radius: 50%;
    background: rgba(77, 181, 255, 0.1);
    backdrop-filter: blur(10px);
}

.header__socials a:hover {
    background: var(--color-primary);
    color: var(--color-bg);
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(77, 181, 255, 0.3);
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ME */

.me{
    background: linear-gradient(45deg, var(--color-primary), #00d4ff, var(--color-primary));
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    box-shadow: 0 20px 40px rgba(77, 181, 255, 0.3);
    transition: var(--transition);
}

.me::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, transparent, rgba(255,255,255,0.1), transparent);
    transform: translateX(-100%);
    animation: shimmer 3s infinite;
}

@keyframes shimmer {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

/* Scoll Down */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    transition: var(--transition);
    animation: bounce 2s infinite;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: rotate(90deg) translateY(0); }
    40% { transform: rotate(90deg) translateY(-10px); }
    60% { transform: rotate(90deg) translateY(-5px); }
}

/* media queries medium devices */
@media screen and (max-width:1024px){
   header{
      height: 68vh;

    }
  }

  /* media queries sma;l devices */
@media screen and (max-width:600px){
 header{
    height: 100vh;
 }   

 .header__socials,
 .scroll__down {
    display: none;
 }
}